* {
    font-family: 'Red Hat Display', sans-serif;
}

.container {
    max-width: 1200px;
    display: block;
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 0 15px;
    box-sizing: border-box;
}

ul {
    list-style-type: none;
    padding: 0;
}

a {
    text-decoration: none;
}

/* ******* HEADER   ******** */
.header {
    display: block;
    background-color: #0c0f16;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
}

.header .header-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0;
}

.header .header-inner .logo {
    display: block;
}

.header .header-inner .logo a img {
    display: block;
    width: 100%;
    transition: .5s;
    /* height: auto; */
}

.header .header-inner .menu {
    display: block;
}

.header .header-inner .menu ul {
    display: block;
    padding-top: 15px;
}

.header .header-inner .menu ul li {
    display: inline;
    padding: 15px;

}

.header .header-inner .menu ul li a {
    display: inline;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 500;
    color: aliceblue;

}

.header .header-inner .menu ul .active {
    border-bottom: 2px solid #027dc0;
}

.header .header-inner .menu ul li:hover>a {
    border-bottom: 2px solid #027dc0;
}

.header .header-inner .btn {
    display: block;
}

.header .header-inner .btn a {
    display: inline-block;
    padding: 10px 20px;
    color: aliceblue;
    border: none;
    border-radius: 50px;
    font-weight: 500;
    background-color: #027dc0;
    /* transition: ease-in-out 0.5s; */
    transition: all ease-in-out 0.4s;

}

.header .header-inner .btn a:hover {
    background-color: transparent;
    border: 2px solid #027dc0;
    box-shadow: 0px 0px 10px 0px #027dc0;
}




.header .header-inner .menu .dropdown {
    display: inline;
    position: relative;
}

.header .header-inner .menu .dropdown i {
    font-size: 14px;
}

.header .header-inner .dropdown>.drop-menu {
    display: none;
    position: absolute;
    background-color: #ffffff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    left: 20px;
    top: 50px;
    min-width: 250px;
    z-index: 1;
    border-top: 2px solid #027dc0;
}

.header .header-inner .dropdown .drop-menu>li {
    display: block;
    padding: 15px;
}

.header .header-inner .dropdown .drop-menu li>a {
    font-size: 14px;
    color: #2a2f35;
    text-transform: capitalize;
    padding: 0 !important;
    display: block;
}

.header .header-inner .dropdown .drop-menu li:hover>a {
    border: none !important;
}

.header .header-inner .dropdown .drop-menu li:hover {
    background-color: #027dc0;
}

.header .header-inner .dropdown:hover .drop-menu {
    display: block;
}

.header-top {
    display: none;
}

/* ***************** */

@media(max-width:991px) {

    .header {
        display: none;
    }

    .header-top {
        display: block;
    }

    .header-top {
        display: block;
          background-color: #0c0f16;

    }

    .header-top .header-inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0;
    }

    .header-top .header-inner .logo {
        color: #ffffff;
        padding: 0 10px;

    }

    .header-top .header-inner .logo>a {
        font-size: 28px;
        font-weight: 600;
        color: #027dc0;
    }

    .header-top .header-inner .bar {
        display: block;
        padding: 10px;
    }

    .header-top .header-inner .bar i {
        font-size: 26px;
        color: #027dc0;
    }

    .header-top .list {
        display: block;
    }

    .header-top .list ul {
        display: block;
        background-color: #0c0f16 !important;
    }

    .header-top .list ul>li {
        display: block;
        padding: 10px;
        /* border: 1px solid aliceblue; */
    }

    .header-top .bar button {
        border: none;
        background-color: transparent;
    }

    .header-top .list ul li a {
        display: block;
        text-transform: capitalize;
        color: aliceblue;
    }

    .header-top .list .dropdown {
        display: block;
    }

    .header-top .list .dropdown button {
        /* float: right; */
        border: none;
        background-color: transparent;
    }

    .header-top .list .dropdown .drop-menu {
        /* background: rgba(255, 255, 255, 0.25)!important; */
        display: block;
    }

}


/* ******  SLIDER  ****** */
.slider {
    display: block;
    background-color: #1e2128;
    padding: 40px 0;
}

.slider .inner-block {
    display: block;
    margin: 0 -15px;
}

.slider .inner-block .slider-inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.slider .inner-block .slider-inner .main-img {
    display: block;
    width: 50%;
    padding: 15px;
    box-sizing: border-box;
    animation-name: animate;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.slider .inner-block .slider-inner .main-img img {
    display: block;
    width: 100%;
    height: auto;
}

@keyframes animate {
    0% {
        transform: translateY(-30px);
    }

    50% {
        transform: translateY(-15px);
    }

    100% {
        transform: translateY(-30px);
    }
}

.slider .inner-block .slider-inner .text {
    width: 50%;
    padding: 0 15px;
    box-sizing: border-box;
}


.slider .inner-block .slider-inner .text b {
    font-size: 18px;
    color: #027dc0;
    font-weight: 400;
    /* position: relative;
    -webkit-text-stroke: 1px #027dc0; */

}

.slider .inner-block .slider-inner .text h1 {
    font-size: 48px;
    line-height: 65px;
    margin: 0;
    color: aliceblue;
    font-weight: 500;
}


.slider .inner-block .slider-inner .text h1 span {
    color: #00b6e8 !important;
}

.slider .inner-block .slider-inner .text .type-text {
    position: relative;
}

.type-text::after {
    content: '';
    display: block;
    position: absolute;
    right: -5px;
    top: 0;
    height: 100%;
    width: 2px;
    animation: type .5s infinite;
    -webkit-animation: type .5s infinite;
}

@-webkit-keyframes type {
    0% {
        opacity: 1;
    }

    50% {
        opacity: .5;
    }

    100% {
        opacity: 0;
    }
}

@keyframes type {
    0% {
        opacity: 1;
    }

    50% {
        opacity: .5;
    }

    100% {
        opacity: 0;
    }
}


.slider .inner-block .slider-inner .text b {
    display: block;
    color: #027dc0;
    font-weight: 500;
}

.typewriter {
    width: 21.5ch;
    white-space: nowrap;
    overflow: hidden;
    animation: cursor 1s step-start infinite,
        text 1s steps(18) alternate infinite;
}

.slider .inner-block .slider-inner .text p {
    font-size: 18px;
    line-height: 28px;
    color: azure;
    font-weight: 200;
    padding-bottom: 5px;
}

.slider .inner-block .slider-inner .text a {
    background-color: #027dc0;
    display: inline-block;
    padding: 15px 30px;
    border-radius: 50px;
    color: aliceblue;
    transition: all 0.5s ease-in;
}

.slider .inner-block .slider-inner .text a:hover {
    background-color: transparent;
    border: 2px solid #027dc0;
    box-shadow: 0px 0px 10px 0px #027dc0;

}

.slider .inner-block .slider-inner .text a i {
    padding: 0 5px;
}

@media(max-width:1199px) {
    .slider .inner-block .slider-inner .text h1 {
        font-size: 40px;
        line-height: 50px;
    }

    .slider .inner-block .slider-inner .text p {
        font-size: 16px;
        line-height: 30px;
        padding-bottom: 0px;
    }
}

@media(max-width:991px) {
    .slider .inner-block .slider-inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center !important;
        justify-content: center;
    }

    .slider .inner-block .slider-inner .main-img {
        width: 100%;

    }

    .slider .inner-block .slider-inner .text {
        width: 100%;
    }
}

@media(max-width:639px) {
    .slider .inner-block .slider-inner .text h1 {
        font-size: 36px;
        line-height: 45px;
    }

    .slider .inner-block .slider-inner .text p {
        font-size: 14px;
        line-height: 26px;
        padding-bottom: 0px;
    }
}

@media(max-width:479px) {
    .slider .inner-block .slider-inner .text h1 {
        font-size: 20px;
        line-height: 28px;
    }

}





/* ***   OUR  ** */

.our {
