    padding: 30px 0;
}

.our .inner-block .our-inner .cards {
    display: block;
    width: 33.33%;
    padding: 15px;
    box-sizing: border-box;
    transition: all 0.2s ease-in;

}

.our .inner-block .our-inner .cards .cards-main {
    display: block;
    padding: 15px;
    border-radius: 15px;

    border: 1px solid #027dc0;
}


.our .inner-block .our-inner .cards .cards-main .cards-img {
    display: inline-block;
    width: 75px;
    height: 75px;
