    background-color: #027dc0;
    position: relative;
}

.our .inner-block .our-inner .cards .cards-main .cards-img .icon-blog {
    display: none;
    position: absolute;
    top: 1%;
    left: 14%;
}

.our .inner-block .our-inner .cards:hover .cards-main .cards-img {
    background-color: aliceblue;
}

.our .inner-block .our-inner .cards .cards-main .cards-img .icon {
    display: block;
}

.our .inner-block .our-inner .cards:hover .cards-img .icon {
    display: none;
}

.our .inner-block .our-inner .cards:hover .cards-main .cards-img .icon-blog {
    display: block;
}

.our .inner-block .our-inner .cards .cards-main:hover {
    box-shadow: 0px 0px 10px 0px #027dc0;
}


.our .inner-block .our-inner .cards .cards-main .cards-img a img {
    display: block;
    width: 50px;
    color: #027dc0;
    margin: 0 auto;
}

.our .inner-block .our-inner .cards .cards-main .title {
    display: block;
    color: aliceblue;
}

.our .inner-block .our-inner .cards .cards-main .title h3 {
